import React, { useState, useEffect } from 'react';
import { fabric } from 'fabric';
import styled from 'styled-components';
import 'react-responsive-modal/styles.css';
import {background, hands, panties,accessories} from './ImagesPool';
import axios from 'axios';
import Modal from "./Modal/Modal";
import Dropdown from 'react-bootstrap/Dropdown'

interface ConnectProps {
  web3: [],
  account: string
}


const NFTO: React.FC<ConnectProps> = ({ web3,account}) => {

  const [colorText, setcolorText] = useState('color')   
  const [canvas, setCanvas] = useState('')   
  const [back, setbackground] = useState('')
  const [text, settext] = useState('')    
  const [hand, sethands] = useState('')
  const [pant, setpanties] = useState('')
  const [access, setaccessories] = useState('')

  const [textToPut, settextToPut] = useState("")

  const [show, setShow] = useState(false)

  const [sign,SetSign] = useState(false);
  const [checkSign,SetCheckSign] = useState(false);

  const [imgURL, setImgURL] = useState('');


 function updateNFT(){
        let value = document.getElementById("textToPut");
        settextToPut(value.value)
 }


 function updateColor(color){
        setcolorText(color)
 }


  useEffect(() => {

    async function CheckSignMsg()  {
            axios({
                  url: "https://api.dirtypanties.xyz/api/v1/usedNFT0/"+account,
                  method: 'get'
                   }).then((response) => {
                            if (response.data["sign"] === "OK")
                                {
                                    console.log("CHECK OK");
                                    SetSign(true)
                                    SetCheckSign(false)
                                    setCanvas(initCanvas())
                                }
                           else{
                                SetSign(false)
                                SetCheckSign(true)
                           }
                   });
       }
        if (account && sign === false) {
           CheckSignMsg()
        }
    }, [sign, account, web3 ])


  useEffect(() => {

    async function SignMsg()  {
               let sig1 = await web3.eth.personal.sign("Please sign to enter the DirtyPanties NFT0 DAPP.", account)
               console.log(sig1)
               axios({
                      url: "https://api.dirtypanties.xyz/api/v1/nft0tracking/"+account+"/"+sig1,
                      method: 'get'
                       }).then((response) => {
                                if (response.data["sign"] === "OK")
                                        console.log("OK");
                                        SetCheckSign(false)
                                        window.location.reload()
                                         setCanvas(initCanvas())
                    });
       }
        if (account && checkSign === true) {
            SignMsg()
        }
    }, [checkSign, account, web3 ])


  const initCanvas = () => (

    new fabric.Canvas('canvas', {
      height: 701.4,
      width: 660,
      top: 0,
      preserveObjectStacking: true
    })

  );    

  const addResetText = (canvi) => {

         canvi.remove(text)
  }

  const addText = (canvi, textToDisplay)  => {

            if(text){
                canvi.remove(text)
               }
    

            let Cantext = new fabric.Text(textToDisplay, {
              fontFamily: 'AuroraMedium',
              fill: colorText,
              textAlign: "left",
              fontSize: 30,
              left: 220,
              top: 50,
              selectable: true,
              hasControls: true,
              hasBorders: true
            })

            settext(Cantext)
            canvas.add(Cantext)
            canvas.setActiveObject(Cantext)
            canvas.renderAll()
    }



  const addRect = (canvi,sticker)  => {

          if(back)
            canvi.remove(back)

        fabric.Image.fromURL(sticker, function (myImg) {
          const img1 = myImg.set({
            left: 0,
            top: 0,
            angle: 0,
            hasControls: false,
            hasBorders: false,
            selectable: false,
            scaleX: (canvi.width/myImg.width)*.8,
            scaleY: (canvi.height/myImg.height)*.8,
          })
            canvas.setBackgroundImage(img1, function() {
                setbackground(img1);
                canvas.renderAll()
          });

        });

  }


  const addRectPanties = (canvi,sticker)  => {

         if(pant)
           canvi.remove(pant)

        fabric.Image.fromURL(sticker, function (myImg) {
          const img1 = myImg.set({
            left: 0,
            top: 0,
            angle: 0,
            hasControls: false,
            hasBorders: false,
            selectable: false,
            scaleX: (canvi.width/myImg.width)*.8,
            scaleY: (canvi.height/myImg.height)*.8,
          })
           setpanties(img1);
           canvi.add(img1)
           canvi.renderAll()
        });

  }

  const addRectAccessories = (canvi,sticker)  => {

         if(access)
            canvi.remove(access)

        fabric.Image.fromURL(sticker, function (myImg) {
          const img1 = myImg.set({
            left: 0,
            top: 0,
            angle: 0,
            hasControls: false,
            hasBorders: false,
            selectable: false,
            scaleX: (canvi.width/myImg.width)*.8,
            scaleY: (canvi.height/myImg.height)*.8,
          })
          setaccessories(img1)
            canvas.setOverlayImage(img1, function() {
            canvas.renderAll()
          });
        });

    }

  const addRectHands = (canvi,sticker)  => {
        if(hand)
            canvi.remove(hand)
 
        fabric.Image.fromURL(sticker, function (myImg) {
          const img1 = myImg.set({
            left: 0,
            top: 0,
            angle: 0,
            hasControls: false,
            hasBorders: false,
            selectable: false,
            scaleX: (canvi.width/myImg.width)*.8,
            scaleY: (canvi.height/myImg.height)*.8,
          })
         sethands(img1)
          canvi.add(img1)
          canvi.renderAll()
       });
   }

    const addImg = (e, canvi) => {

        if(back)
          canvi.remove(back)

        var reader = new FileReader();
        reader.onload = function(e) {
        var image = new Image();
        image.src = e.target.result;

        image.onload = function() {
          var img = new fabric.Image(image);

          img.set({
            left: 0,
            top: 0,
            angle: 0,
            hasControls: false,
            hasBorders: false,
            selectable: false,
            scaleX: (canvi.width/img.width)*.8,
            scaleY: (canvi.height/img.height)*.8,
          });

            canvas.setBackgroundImage(img, function() {
                setbackground(img);
                canvas.renderAll()
          });

        }
      }
      setImgURL(e.target.files[0]["name"])
      console.log(imgURL)
      reader.readAsDataURL(e.target.files[0]);

     }



  return(
    <Wrapper>
    {web3 && sign &&
        <>
              <Header />
              <InputDiv>
                 <TitleLabel>
                    Create your own Panty
                 </TitleLabel>
             </InputDiv>
              <But>
                 <Dropdowndiv>
                      <Dropdown>
                          <Dropdown.Toggle variant="transparent" id="dropdown-toggle" style={{marginTop: -0.2 + 'em'}}>
                            <StyledLink>Backgrounds</StyledLink>
                          </Dropdown.Toggle>
                          <Dropdown.Menu variant="dark" id="dropdown-menu">
                            {background.map((item) => (
                                <Dropdown.Item key={item.src} id="dropdown-nft0" ><StyledLinkUnder> <ImgDiv src={item.src} onClick={() => addRect(canvas,item.src)} />  </StyledLinkUnder></Dropdown.Item>
                              ))}
                          </Dropdown.Menu>
                        </Dropdown>

                      <Dropdown>
                          <Dropdown.Toggle variant="transparent" id="dropdown-toggle" style={{marginTop: -0.2 + 'em'}}>
                            <StyledLink>Panties</StyledLink>
                          </Dropdown.Toggle>
                          <Dropdown.Menu variant="dark" id="dropdown-menu">
                            {panties.map((item) => (
                                <Dropdown.Item key={item.src} id="dropdown-nft0" ><StyledLinkUnder> <ImgDivPanties src={item.src} onClick={() => addRectPanties(canvas,item.src)} />  </StyledLinkUnder></Dropdown.Item>
                              ))}
                          </Dropdown.Menu>
                        </Dropdown>

                      <Dropdown>
                          <Dropdown.Toggle variant="transparent" id="dropdown-toggle" style={{marginTop: -0.2 + 'em'}}>
                            <StyledLink>Hands</StyledLink>
                          </Dropdown.Toggle>
                          <Dropdown.Menu variant="dark" id="dropdown-menu">
                            {hands.map((item) => (
                                <Dropdown.Item key={item.name} id="dropdown-nft0" ><StyledLinkUnder> <ImgDivPanties src={item.src} onClick={() => addRectHands(canvas,item.src)} />  </StyledLinkUnder></Dropdown.Item>
                              ))}
                          </Dropdown.Menu>
                        </Dropdown>

                      <Dropdown>
                          <Dropdown.Toggle variant="transparent" id="dropdown-toggle" style={{marginTop: -0.2 + 'em'}}>
                            <StyledLink>Accessories</StyledLink>
                          </Dropdown.Toggle>
                          <Dropdown.Menu variant="dark" id="dropdown-menu">
                            {accessories.map((item) => (
                                <Dropdown.Item key={item.name} id="dropdown-nft0" ><StyledLinkUnder onClick={() => addRectAccessories(canvas,item.src)}> {item.name} </StyledLinkUnder></Dropdown.Item>
                              ))}
                          </Dropdown.Menu>
                        </Dropdown>
                        <ButtonDiv>
                           <MinInput
                                 id="upload-photo"
                                 type="file"
                                 onChange={(e) => addImg(e, canvas)}
                                 accept="image/png, image/jpeg"
                             />
                                <Dropdown.Toggle variant="transparent" id="dropdown-toggle" style={{marginTop: -0.2 + 'em'}}>
                                     <StyledLink> Upload your PFP</StyledLink>
                                </Dropdown.Toggle>
                               <Dropdown.Toggle variant="transparent" id="dropdown-toggle" style={{marginTop: -0.2 + 'em'}}>
                                     <StyledLink  onClick={() => setShow(true)} > Add Text</StyledLink>
                                </Dropdown.Toggle>
                                      <Modal title="Type the text you want to add to the NFT" onClose={() => setShow(false)} show={show}>
                                       <InsideModal>   
                                                <MintInput id="textToPut" onChange={() => updateNFT()} placeholder="Type here" />  
                                                    <StyledLinkText onClick={() => addText(canvas,textToPut)}> Apply </StyledLinkText>  
                                                    <StyledLinkText onClick={() => addResetText(canvas)}> Reset </StyledLinkText>

                                                    <Dropdown>
                                                        <Dropdown.Toggle variant="transparent" id="dropdown-toggle" style={{marginTop: -0.2 + 'em'}}>
                                                           {colorText}
                                                        </Dropdown.Toggle>
                                                      <Dropdown.Menu variant="dark" id="dropdown-menu">
                                                            <Dropdown.Item id="dropdown-item"><StyledLinkUnder onClick={() => updateColor("White")}> White</StyledLinkUnder></Dropdown.Item>
                                                            <Dropdown.Item id="dropdown-item"><StyledLinkUnder onClick={() => updateColor("Black")}> Black</StyledLinkUnder></Dropdown.Item>
                                                            <Dropdown.Item id="dropdown-item"><StyledLinkUnder onClick={() => updateColor("Red")}> Red</StyledLinkUnder></Dropdown.Item>
                                                            <Dropdown.Item id="dropdown-item"><StyledLinkUnder onClick={() => updateColor("Yellow")}> Yellow</StyledLinkUnder></Dropdown.Item>
                                                      </Dropdown.Menu>
                                                    </Dropdown>
                                        </InsideModal>
                                      </Modal>
                         </ButtonDiv>
                 </Dropdowndiv>

                         <CanvasDiv id="canvas"   />
              </But>
         </>
  }
  {sign && !web3 &&
        <ConnectWalletWrapper>
           <Wrapper>
             <Title>
             "Unlock Your Metamask or click Connect"
              </Title>
           </Wrapper>
        </ConnectWalletWrapper>
  }
  {!sign && web3 &&
            <ConnectWalletWrapper>
               <Wrapper>
                 <Title>
                 "Wallet not signed yet or signature invalid"
                  </Title>
               </Wrapper>
            </ConnectWalletWrapper>
    }

  </Wrapper>
  );
}



