import React, { useState, useEffect } from 'react';
import './App.css';
import 'react-responsive-modal/styles.css';


const LeProjet: React.FC = () => {


  const [width, setWidth]   = useState(window.innerWidth+"px");
  const [height, setHeight] = useState(window.innerHeight+"px");

  const updateDimensions = () => {
    setWidth(window.innerWidth+"px");
    setHeight(window.innerHeight+"px");
  }

  useEffect(() => {
    window.addEventListener("resize", updateDimensions);
    return () => window.removeEventListener("resize", updateDimensions);
  }, []);

   
  console.log(width,height)

  return (
   ( 
       <iframe
        title="website"
        src="https://www.dirtypanties.art/leprojet"
        width={width}
        height={height}
        mozallowfullscreen='true'
        marginheight='0' 
        frameborder="0"
        scrolling="yes" 
        display="block"
        marginwidth='0'
        allowfullscreen='true'
        style={{overflow:"hidden",marginRight: 0, marginLeft:0, border: "none" , display: "block"}}
        />
      )
  )
}


export default LeProjet;
